.shop-cart {
    position: relative;
    width: 40px;
}

.cart-button {
    margin: 0 10px;
    cursor: pointer;
    background-image: url('../img/shopping.svg');
    background-size: 40px;
    min-width: 40px;
    min-height: 40px;
    text-align: center;
    transition: all 0.1s ease-in-out;
}
.cart-body {
    background: linear-gradient(to bottom, #323232 0%, #3F3F3F 40%, #1C1C1C 150%), linear-gradient(to top, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.25) 200%);
    background-blend-mode: multiply; 
    width: 58vh;
    position: fixed;
    top: 10vh;
    right: 0px;
    
    height: 0%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    opacity: 0;
    transition: all 0.4s ease-in-out;
    
}
.cart-lister {
    width: 57vh;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: auto;
    height: 80vh;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.cart-lister::-webkit-scrollbar {
    display: none;
  }
.cart-hide {
    height: 100%;
    opacity: 1;
    overflow: auto;
    box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.425);
}
.cart-body li {
    display: grid;
    grid-template-columns: 70% 7% 18% 5%;
    width: 100%;
    height: 7%;
    background: rgba(70, 70, 70, 0);
    color:rgb(255, 255, 255);
    font-weight:lighter;
    transition: background-color 0.4s ease-in-out;
    padding: 4px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.082);
}
.cart-body li:hover {
    background-color: rgb(85, 85, 85);
}
.cart-body li input {
    max-width: 15px;
    text-align: center;
    margin: 0 1px;
    background: none;
    border: none;
    outline: none;
    color:rgb(255, 255, 255);
    font-weight:lighter;

}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
    -moz-appearance: textfield;
}

.name {
    grid-column: 1;
    display: flex;
    align-items: center;
    padding-left: 5px;
    padding-right: 5px; 
    font-size: 16px;
    text-overflow: clip;
}
.val {
    grid-column: 2;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 5px;
}
.plus, .minus, .del {
    cursor: pointer;
    margin: 0 5px;
}
.cost {
    grid-column: 3;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 18px;
}
.del {
    grid-column: 4;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 22px;
}
.total {
    width: 50px;
    height: 50px;
    border: none;
    background-image:url('../img/shopping-bag.svg');
    background-size: cover;
    background-color: none;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
}
.total-wrap {
    margin-top: 10px;
    width: 100%;
    height: 9vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.hide {
    display: none;
}
.cart-button:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 100%;
    top: 100%;
    z-index: 1;
    background-color: white;
    border: 1px solid rgb(36, 36, 36);
    color: black;
    font-weight: 100;
    font-size: 14px;
    padding: 5px;
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .cart-lister {
        width: 100%;
    }
    .cart-body { 
        width: 100%;
    }
    .total {
        width: 40px;
        height: 40px;
    }
}