@import url('https://fonts.googleapis.com/css2?family=Amatic+SC&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poiret+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Cormorant:wght@300&display=swap');

.dish-container {
    height: 100%;
    width: 100%;
    overflow: auto;
    padding: 0;
    font-family: 'Poiret One', cursive; 
    margin: 0 auto;
}
.dish-container h1 {
    font-size: 24px;
    margin: 0 auto;
    margin-top: 15px;
    width: 100%;
    text-align: center;
    cursor: pointer;
}
.cards-wrap {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    margin: 0 auto;
    margin-top: 20px;
    width: 125vh;
    /* background-color: rgba(255, 255, 255, 0.918);
    box-shadow: 1px 1px 10px rgba(36, 36, 36, 0.178) ; */

}
.dish-card {
    width: 125vh;
    height: auto;
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-columns:  auto auto;
    margin: 5px 2px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 2px 2px 7px rgba(36, 36, 36, 0.178) ;
    padding: 10px;
}
.dick-pic {
    width: 150px;
    height: 150px;
    overflow: hidden;
    grid-column: 1;
    grid-row: 1/4;
    border-radius: 50%;
    margin: 0 auto;
}
.dish-name {
    margin: 0 auto;
    font-size: 26px;
    text-align: center;
    grid-column: 2/3;
    grid-row: 1;
}
.foot {
    margin-left: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 26px;
    grid-row: 3/3;
    grid-column: 2/2;
}
.add-to-cart {
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-image: url('../img/add-to-basket.svg');
    background-size: cover;
    margin-left: 50px;
    margin-right: 15px;
}
.hide {
    display: none;
}
.dish-desc {
    font-size: 22px;
    grid-column: 2;
    grid-row: 2;
    width: 90vh;
    margin: 0 auto;
}
@media only screen and (min-width : 800px) and (max-width : 1280px) {
    .dish-card{
        width: 90%;
    }
    .cards-wrap{
        width: 90%;
    }
    .dish-desc{
        width: auto;
        margin: 0 10px;
        font-size: 18px;
    }
    .foot{
        font-size: 20px;
    }
}

@media only screen and (min-width : 640px) and (max-width : 800px) {
    .dish-card{
        width: 100%;
        margin:0;
    }
    .cards-wrap{
        width: 100%;
    }
    .dish-desc{
        width: auto;
        margin: 0 10px;
        font-size: 18px;
    }
    .foot{
        font-size: 20px;
    }
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .dish-card{
        width: 100%;
        margin:0;
    }
    .cards-wrap{
        width: 100%;
    }
    .dish-desc{
        width: auto;
        margin: 0 5px;
        font-size: 16px;
    }
    .foot{
        font-size: 18px;
    }
    .add-to-cart {
        width: 30px;
        height: 30px;
        cursor: pointer;
        margin-left: 30px;
        margin-right: 30px;
    }
    .dick-pic {
        width: 100px;
        height: 100px;
        overflow: hidden;
        grid-column: 1;
        grid-row: 1/4;
        border-radius: 50%;
        margin: 0 auto;
    }
    .dish-name {
        font-size: 18px;
    }
}