.slide-container {
    z-index: 0;
    width: 100%;
    height: 90vh;
    position: fixed;
    -webkit-filter: blur(4px);
            filter: blur(4px);
    opacity: 0.7;
}
.slide-box {
    width: 100%;
    height: 100%;
}
.slide {
    width: 100%;
    height: auto;
    overflow: hidden;
}
.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }
  
  @-webkit-keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  
  @keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  @media only screen and (min-width : 640px) and (max-width : 800px) {
    .slide-container, .slide, .slide-box{
      width:auto;
      height: 100vh;
      overflow: hidden;
    }
    
    }
  @media only screen and (min-width : 375px) and (max-width : 640px) {
  .slide-container, .slide, .slide-box{
    width:auto;
    height: 100vh;
    overflow: hidden;
  }
  
  }
.header-container {
    position: fixed;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    height: 10vh;
    width: 100%;
    background: linear-gradient(to bottom, #323232 0%, #3F3F3F 40%, #1C1C1C 150%), linear-gradient(to top, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.25) 200%);
    background-blend-mode: multiply;    
    font-family: 'Comfortaa', cursive;
    font-weight: lighter;
    z-index: 999;
    color: white;
    box-shadow: 2px 2px 7px 1px rgba(0, 0, 0, 0.3);
}
a {
    text-decoration: none;
    color: white;
}
.header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.header {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100vh;
    margin: 0;
    padding: 0;
}
.header li {
    width: 100%;
    height: 100%;
    margin: 0 auto;
    text-align: center;
}
.logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;   
    font-family: 'Raleway', sans-serif;
    
}
h2, h4 {
    margin: 0;
    padding: 0;
    text-align: center;
    color: white;
}
.menu-button-span {
    background-image: url(/static/media/menu.747fea5a.svg);
    background-size: 40px;
    cursor: pointer;
    min-width: 40px;
    min-height: 40px; 
    background-repeat: no-repeat;
    margin: 0 10px;
    position: relative;
}
.menu-button-span:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 100%;
    top: 100%;
    z-index: 1;
    background-color: white;
    border: 1px solid rgb(36, 36, 36);
    color: black;
    font-weight: 100;
    font-size: 14px;
    padding: 5px;
}
.inst {
    background-image: url(/static/media/instagram.9049f452.svg);
    background-size: 40px;
    cursor: pointer;
    min-width: 40px;
    min-height: 40px; 
    background-repeat: no-repeat;
    margin: 0 20px;
    position: relative;
}
.inst:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 100%;
    top: 100%;
    z-index: 1;
    background-color: white;
    border: 1px solid rgb(36, 36, 36);
    color: black;
    font-weight: 100;
    font-size: 14px;
    padding: 5px;
}

@media only screen and (min-width : 800px) and (max-width : 1280px) {
    .header{
        display: none;
    }
}

@media only screen and (min-width : 640px) and (max-width : 800px) {
    .header {
        display: none;
    }
    .logo{
        display: none;
    }
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .header {
        display: none;
    }
    .logo{
        display: none;
    }
}
.shop-cart {
    position: relative;
    width: 40px;
}

.cart-button {
    margin: 0 10px;
    cursor: pointer;
    background-image: url(/static/media/shopping.500a2c41.svg);
    background-size: 40px;
    min-width: 40px;
    min-height: 40px;
    text-align: center;
    transition: all 0.1s ease-in-out;
}
.cart-body {
    background: linear-gradient(to bottom, #323232 0%, #3F3F3F 40%, #1C1C1C 150%), linear-gradient(to top, rgba(255,255,255,0.40) 0%, rgba(0,0,0,0.25) 200%);
    background-blend-mode: multiply; 
    width: 58vh;
    position: fixed;
    top: 10vh;
    right: 0px;
    
    height: 0%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    opacity: 0;
    transition: all 0.4s ease-in-out;
    
}
.cart-lister {
    width: 57vh;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: auto;
    height: 80vh;
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
    scrollbar-width: none;
    -ms-overflow-style: none;
}
.cart-lister::-webkit-scrollbar {
    display: none;
  }
.cart-hide {
    height: 100%;
    opacity: 1;
    overflow: auto;
    box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.425);
}
.cart-body li {
    display: grid;
    grid-template-columns: 70% 7% 18% 5%;
    width: 100%;
    height: 7%;
    background: rgba(70, 70, 70, 0);
    color:rgb(255, 255, 255);
    font-weight:lighter;
    transition: background-color 0.4s ease-in-out;
    padding: 4px 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.082);
}
.cart-body li:hover {
    background-color: rgb(85, 85, 85);
}
.cart-body li input {
    max-width: 15px;
    text-align: center;
    margin: 0 1px;
    background: none;
    border: none;
    outline: none;
    color:rgb(255, 255, 255);
    font-weight:lighter;

}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type=number] {
    -moz-appearance: textfield;
}

.name {
    grid-column: 1;
    display: flex;
    align-items: center;
    padding-left: 5px;
    padding-right: 5px; 
    font-size: 16px;
    text-overflow: clip;
}
.val {
    grid-column: 2;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    margin: 0 5px;
}
.plus, .minus, .del {
    cursor: pointer;
    margin: 0 5px;
}
.cost {
    grid-column: 3;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 18px;
}
.del {
    grid-column: 4;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size: 22px;
}
.total {
    width: 50px;
    height: 50px;
    border: none;
    background-image:url(/static/media/shopping-bag.dcc20037.svg);
    background-size: cover;
    background-color: none;
    text-align: center;
    font-size: 18px;
    cursor: pointer;
}
.total-wrap {
    margin-top: 10px;
    width: 100%;
    height: 9vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    
}
.hide {
    display: none;
}
.cart-button:hover::after {
    content: attr(data-title);
    position: absolute;
    left: 100%;
    top: 100%;
    z-index: 1;
    background-color: white;
    border: 1px solid rgb(36, 36, 36);
    color: black;
    font-weight: 100;
    font-size: 14px;
    padding: 5px;
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .cart-lister {
        width: 100%;
    }
    .cart-body { 
        width: 100%;
    }
    .total {
        width: 40px;
        height: 40px;
    }
}
.dish-container {
    height: 100%;
    width: 100%;
    overflow: auto;
    padding: 0;
    font-family: 'Poiret One', cursive; 
    margin: 0 auto;
}
.dish-container h1 {
    font-size: 24px;
    margin: 0 auto;
    margin-top: 15px;
    width: 100%;
    text-align: center;
    cursor: pointer;
}
.cards-wrap {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    margin: 0 auto;
    margin-top: 20px;
    width: 125vh;
    /* background-color: rgba(255, 255, 255, 0.918);
    box-shadow: 1px 1px 10px rgba(36, 36, 36, 0.178) ; */

}
.dish-card {
    width: 125vh;
    height: auto;
    display: grid;
    grid-template-rows: auto auto auto;
    grid-template-columns:  auto auto;
    margin: 5px 2px;
    background-color: rgba(255, 255, 255, 0.8);
    box-shadow: 2px 2px 7px rgba(36, 36, 36, 0.178) ;
    padding: 10px;
}
.dick-pic {
    width: 150px;
    height: 150px;
    overflow: hidden;
    grid-column: 1;
    grid-row: 1/4;
    border-radius: 50%;
    margin: 0 auto;
}
.dish-name {
    margin: 0 auto;
    font-size: 26px;
    text-align: center;
    grid-column: 2/3;
    grid-row: 1;
}
.foot {
    margin-left: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 26px;
    grid-row: 3/3;
    grid-column: 2/2;
}
.add-to-cart {
    width: 40px;
    height: 40px;
    cursor: pointer;
    background-image: url(/static/media/add-to-basket.415280ff.svg);
    background-size: cover;
    margin-left: 50px;
    margin-right: 15px;
}
.hide {
    display: none;
}
.dish-desc {
    font-size: 22px;
    grid-column: 2;
    grid-row: 2;
    width: 90vh;
    margin: 0 auto;
}
@media only screen and (min-width : 800px) and (max-width : 1280px) {
    .dish-card{
        width: 90%;
    }
    .cards-wrap{
        width: 90%;
    }
    .dish-desc{
        width: auto;
        margin: 0 10px;
        font-size: 18px;
    }
    .foot{
        font-size: 20px;
    }
}

@media only screen and (min-width : 640px) and (max-width : 800px) {
    .dish-card{
        width: 100%;
        margin:0;
    }
    .cards-wrap{
        width: 100%;
    }
    .dish-desc{
        width: auto;
        margin: 0 10px;
        font-size: 18px;
    }
    .foot{
        font-size: 20px;
    }
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .dish-card{
        width: 100%;
        margin:0;
    }
    .cards-wrap{
        width: 100%;
    }
    .dish-desc{
        width: auto;
        margin: 0 5px;
        font-size: 16px;
    }
    .foot{
        font-size: 18px;
    }
    .add-to-cart {
        width: 30px;
        height: 30px;
        cursor: pointer;
        margin-left: 30px;
        margin-right: 30px;
    }
    .dick-pic {
        width: 100px;
