.admin-page {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 50% 50%;
}
.add-section, .orders-section {
    padding: 15px;
    height: 85vh;
    overflow: auto;
}
.add-form {
    display: flex;
    justify-content: space-between;
    align-items: left;
    flex-direction: column;
    width: 500px;
}
label {
    display: grid;
    grid-template-columns: 25% auto;
    margin: 5px 0;
}
input {
    width: 300px;
    border: none;
    border-radius: 5px;
}
.hidden-element {
    display: none;
}
select {
    margin: 5px 0;
    margin-left: 25%;
    height: 20px;
    width: 304px;
    border: none;
    border-radius: 5px;
}
.menu-list-admin {
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
    border: none;
}
.menu-list-admin select {
    
    margin: 10px 0;
}

.remove {
    width: 70px;
    background-color: none;
    cursor: pointer;
    border: none;
    margin: 0;
}
.order-list .ordered-item {
    list-style: none;
    width: 100%;
    height: auto;
    margin: 0;
    padding: 0;
}

.order-list .ordered-item li {
    display: grid;
    grid-template-columns: auto 5% 10%;
    align-items: center;
    width: 100%;
    padding: 2px 5px;
}
.bordered {
    border: 1px solid black;
    padding: 2px 5px;
}
.head-sec {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.complete-button {
    cursor: pointer;
    color: rgba(0, 75, 0, 0.842);
    text-decoration: underline;
}
.complete-order {
    text-decoration: line-through;
    color: rgba(77, 77, 77, 0.5);
}
.consum {
    font-weight: bolder;
    color: rgba(121, 0, 0, 0.849)
}
.clear-button {
    cursor: pointer;
}
.order-name {
    color: black;
}