.side-menu-container {
    z-index: 998;
    position: fixed;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
    transition: all 0.4s ease-in-out;
    margin-top: 8vh;
    background-color: rgb(0, 0, 0);
    height: 0%;
    opacity: 0;
    pointer-events: none;
}
.closed {
    height: 100%;
    opacity: 1;
    pointer-events: all;
}

.toggle-side {
    /* position: relative; */
    /* left: -75px;
    top: 20px; */
    width: 50vh;
    margin: 10px;
    padding: 0 ;
    transition: all 0.0s ease-in-out;
    height: 25px;
    cursor: pointer;
    font-size: 20px;
    transition: color 0.4s ease-in-out;
}
.toggle-side:hover {
   color:rgb(44, 44, 44)
}
.switched {
    position: relative;
    left: 0px;
    width: 100vh;
    margin: 0 auto;
    padding: 0 ;
    height: 25px;
    background-color: rgb(255, 255, 255);
    text-align: center;
    left:-62%;
    transform: translateY(267px) rotate(-90deg);
}
.switched:hover {
    background-color: rgb(255, 230, 0);

}
.menu-list {
    margin: 0;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    width: 100%;
    padding: 0;
}
.side-li {
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.842);
    transition: all 0.4s ease-in-out;
}
.side-li a {
    font-size: 20px;
    text-decoration: none;
    text-align: center;
    padding: 0 10px;
    color: whitesmoke;
    width: 100%;
    height: 100%;
    padding-top: 200%;
}

#first {
    background-image: url('../img/tomyam.jpg');
    opacity: 0.8;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}
#second {
    background-image: url('../img/sousvide-salmon.jpg');
    opacity: 0.8;
    background-position: center;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}
#salad {
    background-image: url('../img/chukka.jpg');
    opacity: 0.8;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}
#dess {
    background-image: url('../img/cheescake.jpg');
    background-position: center;
    opacity: 0.8;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}#kids {
    background-image: url('../img/pyure.jpg');
    opacity: 0.8;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}
#first a, #second a, #salad a, #dess a, #kids a {
    font-size: 36px;
    text-align: center;
    text-shadow: 2px 2px 5px black;
    font-weight: bolder;
}
#first:hover, #second:hover, #salad:hover, #dess:hover, #kids:hover {
    opacity: 1;
}

@media only screen and (min-width : 640px) and (max-width : 800px) {
    .menu-list{
        width: 100%;
    }
    #first a, #second a, #salad a, #dess a, #kids a {
        font-size: 20px;
        text-align: center;
        text-shadow: 2px 2px 5px black;
        font-weight: bolder;
    }
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .menu-list{
        width: 100%;
        flex-direction: column;
        height: 92vh;
    }
    #first a, #second a, #salad a, #dess a, #kids a {
        font-size: 38px;
        text-align: center;
        text-shadow: 2px 2px 5px black;
        font-weight: bolder;
        width: 100%;
        color: rgb(255, 255, 255);
        z-index: 1000;
    }
    #first , #second , #salad , #dess , #kids  {
        height: 20%;
        background-position: center;
        background-size: 100%;
    }
    .side-li a {
        font-size: 26px;
        text-decoration: none;
        text-align: center;
        padding: 0 10px;
        color: whitesmoke;
        width: 100%;
        height: 100%;
        padding-top: 50px;
    }
}