.home-container {
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0);
    position: relative;
}

.slides-wrap {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.whats-new::before {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    content: '';
}

.whats {
    position: relative;
    margin: auto;
    text-align: center;
    font-size: 88px;
    color: rgb(255, 255, 255);
    z-index: 1;
    width: 100%;
    padding-top: 150px;
}

@media only screen and (min-width : 800px) and (max-width : 1280px) {
    .whats {
        padding-top: 50px;
    }
}

@media only screen and (min-width : 640px) and (max-width : 800px) {
    .whats {
        padding-top: 50px;
        font-size: 38px;
    }
}
@media only screen and (min-width : 375px) and (max-width : 640px) {
    .whats {
        padding-top: 150px;
        font-size: 28px;
    }
}