.slide-container {
    z-index: 0;
    width: 100%;
    height: 90vh;
    position: fixed;
    filter: blur(4px);
    opacity: 0.7;
}
.slide-box {
    width: 100%;
    height: 100%;
}
.slide {
    width: 100%;
    height: auto;
    overflow: hidden;
}
.fade {
    -webkit-animation-name: fade;
    -webkit-animation-duration: 1.5s;
    animation-name: fade;
    animation-duration: 1.5s;
  }
  
  @-webkit-keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  
  @keyframes fade {
    from {opacity: .4} 
    to {opacity: 1}
  }
  @media only screen and (min-width : 640px) and (max-width : 800px) {
    .slide-container, .slide, .slide-box{
      width:auto;
      height: 100vh;
      overflow: hidden;
    }
    
    }
  @media only screen and (min-width : 375px) and (max-width : 640px) {
  .slide-container, .slide, .slide-box{
    width:auto;
    height: 100vh;
    overflow: hidden;
  }
  
  }